.SearchBar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1.94rem;
    margin-bottom: 6.33rem;
   
  }
  
  .SearchBar input {
    width: 300px;
    padding: .88rem 0;
    border: 1px solid #6c41e9b3;
    border-radius: 50px;
    margin-bottom: 2.22rem;
    color: #000000;
    text-align: center;
    font-size: 1rem;
    background-color: #ffffff;
  }
  
  .SearchBar input:focus {
    outline: none;
  }
  
  .SearchButton {
    cursor: pointer;
    width: 8.11rem;
    padding: .77rem 0;
    border-radius: 54px;
    background-color: #43CCa3;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    border: 0px;
    color: rgb(0, 0, 0);
    font-weight: 500;
  }
