body,
html,
#root {
  height: 100%;
}
body {
  background-color: #000000;
}
html {
  font-size: 18px;
}

h1 {
  
  padding: .77rem 0;
  background-color: #000000;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-size: 1.88rem;
  color: #43CCA3;
}

h1 .highlight {
  color: #ffffff;
  
}

.description {
  text-align: center;
  color: #43CCA3;
}

.detail-text {
  color: #ffffff;
  text-align: center;
  font-weight: bold;
}

h2 {
  font-family: 'Poppins', sans-serif;
  font-size: 1.55rem;
}

.App {
  height: 100%;
  padding: 0 17% 10% 17%;
  background-color: #000000;
  background-image: url('/src/components/App/background_photo_desktop.jpeg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'Work Sans', sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px) {
  .App-playlist {
    align-items: center;
    flex-direction: column;
  }
}